
@media screen and (max-width: 1200px) {
.button-container .arrow-container .fill-group {
    fill: rgba(255, 255, 255, 0.9) !important;
}
}
.button-container .arrow-container .fill-group {
  fill: #1E7BBB;
}
.button-container.nav-active .arrow-container .fill-group, .button-container:hover .arrow-container .fill-group, .button-container.call-to-action .arrow-container .fill-group {
  fill: rgba(255, 255, 255, 0.9);
}
.button-container:hover .arrow-container .bracket-left {
  -webkit-transform: translateX(-3px);
          transform: translateX(-3px);
}
.button-container:hover .arrow-container .bracket-right {
  -webkit-transform: translateX(3px);
          transform: translateX(3px);
}



