@import './variables';

body {
  font-family: 'Gotham-Book', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
  cursor: default;
  margin: 0px;
  width: 100%;
  height: 100vh;
  background: $body-bg; // linear-gradient(45deg, #FEF3F3, #E4EBF1);
  // background-size: 400% 400%;
  // animation: Gradient 20s ease infinite;
  @media screen and (max-width: $md-max) {
    height: 100%;
  }
  &.bg-white {
    background: white;
    animation: none;
  }
}

// https://codepen.io/P1N2O/pen/pyBNzX

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

.bg-gradient {
  background: linear-gradient(45deg, #FEF3F3, #E4EBF1);
}

.debug {
  background: $debug;
}

.relative {
  position: relative;
}

.auto {
  margin: auto;
}

.invisible {
  opacity: 0;
}

.upcase {
  text-transform: uppercase;
}

.bold {
  font-weight: bold;
}

// z-indexes
@for $i from 1 through 9 {
  .ind-#{$i} {
    z-index: $i;
  }
}

// top
.ind-top {
  z-index: 9999;
}

.overflow-visible {
  overflow: visible;
}

.overflow-hidden {
  overflow: hidden;
}

.color-orange {
  color: $orange !important;
}

.color-blue {
  color: $blue !important;
}

.border-blue {
  background: $white;
  border: 1px solid $blue;
}

.border-left-right-blue {
  border-left: 1px solid $blue;
  border-right: 1px solid $blue;
}

.color-red {
  color: $red !important;
}

.border-red {
  background: $white;
  border: 1px solid $red;
}

.border-red-important {
  border: 1px solid $red !important;
  &::placeholder {
    color: $red !important;
  }
}

.border-error-container {
  transition: all 0.6s ease-in-out;
}

.border-error {
  position: relative;
  padding-top: 12px;
  padding-bottom: 12px;
  margin-top: 12px;
  margin-bottom: 12px;
  .survey-number, .large, .subheader {
    color: $red !important;
  }
}

.border-error::after {
  position: absolute;
  box-sizing: border-box;
  border: 1px solid $red;
  color: $red;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: calc(100% - 80px - 80px);
  height: 100%;
  pointer-events: none;
  padding: 12px;
  content: 'This field is required';
  @media screen and (max-width: $md-max) {
    width: 98%
  }
}

.border-green {
  background: $white;
  border: 1px solid $green;
}

.fill {
  &.green {
    fill: $green;
  }
  &.red {
    fill: $red;
  }
}

  //- col with rollover only -//

  .border-bg-rollover { // now used for progress selectors
    background: $white;
    border: 1px solid transparent;
    cursor: pointer;
    opacity: 0.75;
    transition: all 0.75s;

    &:hover {
      opacity: 1;
      border: 1px solid $blue;
    }
  }

  // flip card trigger
  .crosshair-rollover-trigger {
    cursor: pointer;
    + .crosshair-container {
      opacity: 0;
    }
    &:hover, &.hovered {
      + .crosshair-container {
        opacity: 1;
      }
    }
  }
  
  .show-sm {
    @media screen and (min-width: $md-break) {
      * {
        display: none;
      }
    }
  }

  .hide-sm {
    @media screen and (max-width: $sm-max) {
      display: none;
    }
  }

  .show-xs {
    @media screen and (min-width: $sm-break) {
      * {
        display: none;
      }
    }
  }

  .hide-xs {
    @media screen and (max-width: $xs-max) {
      display: none;
    }
  }

  .extra-padding-bottom {
    padding-bottom: 144px;
  }
  
  // gutters

  .gutter-horizontal {
    position: relative;
    margin: auto;
    width: calc(100% - 80px - 80px);
    max-width: 1200px;
    transition: max-width $transition-1;
    &.gutter-full {
      width: 100%;
      max-width: 100%;
    }
    @media screen and (max-width: $sm-max) {
      width: 100%;
      max-width: 100%;
    }
  }

  .gutter-horizontal-IEonly {
    position: relative;
    width: calc(100% - 80px - 80px);
    max-width: 1200px;
    transition: max-width $transition-1;
    &.gutter-full {
      width: 100%;
      max-width: 100%;
    }
    @media screen and (max-width: $sm-max) {
      width: 100%;
      max-width: 100%;
    }
  }

    .max-height-vertical {
      position: relative;
      // margin-top: 48px;
      width: 100%;
      height: calc(100vh - 96px);
      max-height: calc(100vh - 96px);
      &.max-height-full {
        margin-top: 0px;
        height: 100%;
        max-height: 100%;
      }
      &.max-height-auto {
        height: auto;
      }
      @media screen and (max-width: $md-max) {
        margin-top: 48px;
        height: auto;
        max-height: 100%;
        margin-bottom: calc(96px + 48px);
        &.max-height-full {
          margin-bottom: 0px;
        }
      }
      @media screen and (max-width: $sm-max) {
        margin-bottom: calc(60px + 48px);
        &.max-height-full {
          margin-bottom: 0px;
        }
      }
    }

    .box-overflow {
      box-sizing: border-box;  
      overflow-x: hidden;
      // overflow-y: auto;
      // transition: border $transition-1;
      border: 1px solid transparent;
      // &.overflow-hidden {
      overflow-y: hidden;
      // }
      // @media screen and (max-height: 600px) {
      //   border: 1px solid $blue;
      // }
      @media screen and (max-width: $md-max) {
        overflow-x: hidden;
        overflow-y: hidden;
        // border: 1px solid transparent;
      }
      // &.box-overflow-active {
      //   border: 1px solid $blue
      // }
    }

    .scroll-outer {
      position: absolute;
      display: flex;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // padding-right: 72px; // removed for testing
      overflow: auto;
      @media screen and (max-width: $md-max) {
        position: relative;
        padding-right: 0px; 
        &.scroll-padding-bottom {
          padding-bottom: 72px;
        }
        &.maintain-outer {
          position: absolute;
          // padding-right: 72px;
          // padding-bottom: 0px;
        }
      }
    }

    .max-100 {
      max-height: 100%;
    }

    .max-98 {
      max-height: 98px;
    }

    .border-box {
      box-sizing: border-box;
    }

    .center-flex {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .center-flex-self {
      display: flex;
      align-self: center;
    }

    .center-flex-mobile {
      @media screen and (max-width: $sm-max) {
        position: relative;
        display: flex;
        justify-content: center;
      }
    }

    .self-center {
      position: relative;
      top: 50%;
      transform: translateY(-50%);
    }

    .share-flip-outer {
      box-sizing: border-box;
      border: 1px solid $blue;
      // overflow: hidden;
      @media screen and (max-width: $md-max) {
        height: auto;
        // min-height: auto !important;
        margin-bottom: 24px;
        &.no-ratio-md {
          height: auto !important
        }
      }
    }
    
    .share-flip-inner {
      position: relative;
      box-sizing: border-box;
      // padding: 48px;
      padding-left: 48px;
      padding-right: 48px;
      padding-top: 12px;
      padding-bottom: 12px;
      margin: auto;
    }

    .transform-up {
      transform: translateY(-48px);
      @media screen and (max-width: $md-max) {
        transform: none;
      }
    }

    .transform-down {
      transform: translateY(48px);
      @media screen and (max-width: $md-max) {
        transform: none;
      }
    }

    .bg-white {
      background-color: $white;
    }

    .no-border {
      border: 1px solid transparent;
    }

    .border-blue-hover {
      &:hover, &.hovered {
        border: 1px solid $blue !important;
      }
    }

    .cover-class {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    .mobile-only {
      @media screen and (min-width: $md-break) {
        display: none;
      }
    }


  // transition in/out classes

  .content-translate {
    transition: all 1s ease;
    &.content-inactive {
      transform: translateY(25%);
      opacity: 0;
      pointer-events: none;
    }
  }

  .content-fade-in {
    transition: all 1s ease;
    &.content-inactive {
      opacity: 0;
    }
  }

  .transition-1 {
    transition: all $transition-1;
  }

  .text-center {
    text-align: center;
  }
  

  .padding-top-96 {
    padding-top: 96px;
  }

  .padding-bottom-96 {
    padding-bottom: 96px;
  }

  .padding-bottom-144 {
    padding-bottom: 144px;
  }

  .padding-bottom-12 {
    padding-bottom: 12px;
  }

  .padding-right-12 {
    padding-right: 12px;
  }

  .margin-top-24 {
    margin-top: 24px;
  }

  .margin-bottom-12 {
    margin-bottom: 12px;
  }

  .margin-left-12-lg {
    @media screen and (min-width: $lg-break) {
      margin-left: 12px !important;
    }
  }

  .margin-right-12-lg {
    @media screen and (min-width: $lg-break) {
      margin-right: 12px !important;
    }
  }

  .margin-right-12-md {
    @media screen and (min-width: $md-break) {
      margin-right: 12px !important;
    }
  }

  .margin-left-12-lg {
    @media screen and (min-width: $lg-break) {
      margin-left: 12px !important;
    }
  }

  .margin-left-12-md {
    @media screen and (min-width: $md-break) {
      margin-left: 12px !important;
    }
  }

  .transform-up-24-lg {
    @media screen and (min-width: $lg-break) {
      transform: translateY(-24px) !important;
    }
  }

  .float-right {
    float: right;
  }

  .form-text-area {
    position: relative;
    margin: auto;
    box-sizing: border-box;
    height: 120px;
    width: 100%;
    padding: 12px;
    margin-top: 12px;
    border: 1px solid $light-blue;
    border-radius: 0;
    -webkit-appearance: none;
    outline: none;
    resize: none;
    transition: border $transition-1;
    &:focus {
      border: 1px solid $blue;
    }
    &.text-area-small {
      height: 48px;
    }
  }


  .hide-all {
    display: none !important;
    * {
      display: none !important;
    }
  }

  .a {
    color: $black !important;
    cursor: pointer;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }

  .big-svg {
    transform: scale(1.5, 1.5);
    margin-bottom: 12px
  }

  .svg-fill-blue {
    fill: $blue;
    g, polygon, rect, {
      fill: $blue;
    }
  }

  .small-text {
    font-size: 12px;
  }


