
.svg-icon-background-small svg {
  width: 24px;
  height: 24px;
}
.front svg g:not(.ignorehover), .back svg g:not(.ignorehover) {
  fill: white;
  transition: all 0.2s ease-in-out;
}
.front:hover svg g, .front.flipped svg g, .back:hover svg g, .back.flipped svg g {
  fill: #1E7BBB;
}



