@import './variables';

html, .root {
  font-size: 16px;
  line-height: 24px;
}

@font-face {
  font-family: 'Gotham-Light';
  src: url('./font-files/Gotham/Gotham-Light.otf') format('opentype'),
       url('./font-files/Gotham/Gotham-Light.ttf') format('truetype'),
       url('./font-files/Gotham/Gotham-Light.woff') format('woff');
}

@font-face {
  font-family: 'Gotham-Medium';
  src: url('./font-files/Gotham/Gotham-Medium.otf') format('opentype'),
       url('./font-files/Gotham/Gotham-Medium.ttf') format('truetype'),
       url('./font-files/Gotham/Gotham-Medium.woff') format('woff');
}

@font-face {
  font-family: 'Gotham-Book';
  src: url('./font-files/Gotham/Gotham-Book.otf') format('opentype'),
       url('./font-files/Gotham/Gotham-Book.ttf') format('truetype'),
       url('./font-files/Gotham/Gotham-Book.woff') format('woff');
}

@font-face {
  font-family: 'Gotham-Black';
  src: url('./font-files/Gotham/Gotham-Black.otf') format('opentype');
}

h1, .h1 {
  font-family: 'Gotham-Book';
  color: $orange;
  font-size: 24px;
  font-size: 1.75rem;
  font-size: calc(1.75rem + 1vw);
  letter-spacing: 2px;
  line-height: 1px;
  line-height: calc(3rem + 1vw);
  &.title { // landing screen
    font-family: 'Gotham-Black';
    font-size: 72px;
    color: $black;
    letter-spacing: 4px;
    line-height: 72px;
  }
}

h2, .h2 {
  font-family: 'Gotham-Book';
  // font-size: 28px;
  color: $orange;
  // letter-spacing: 1px;
  // line-height: 36px;
  font-size: calc(1.4375rem + 0.5vw);
  letter-spacing: 1px;
  line-height: calc(2rem + 0.5vw);
  // margin-top: 1.5rem;
  // margin-bottom: 1.5rem;
}

p, .p {
  font-family: 'Gotham-Book';
  // font-size: 16px;
  font-size: 1rem; // calc(1rem + 0.25vw);
  color: $black;
  letter-spacing: 0.8px;
  line-height: 1.5rem; // calc(1.5rem + 0.25vw);
  &.large {
    font-family: 'Gotham-Book';
    // font-size: 30px;
    color: $orange;
    // letter-spacing: 0;
    // line-height: 44px;
    // @media screen and (max-width: $sm-break) {
    //   font-size: 24px;
    //   line-height: 36px;
    // }
    font-size: calc(1.1875rem + 0.5vw);
    line-height: calc(1.5rem + 0.5vw);
    // margin-top: 1.5rem;
    // margin-bottom: 0rem;
  }
  ol {
    padding-left: 14px;
  }
}

.subheader, p.subheader, .p.subheader {
  font-family: 'Gotham-Medium';
  font-size: 16px;
  color: $black;
  letter-spacing: 0.8px;
}

.landing-header {
  font-family: 'Gotham-Medium';
  font-size: 72px;
  color: $blue;
  letter-spacing: 4px;
  line-height: 72px;
  @media screen and (max-width: $sm-max) {
    font-size: 60px;
  }
}


